import React, { useEffect, useRef, useState } from 'react';
import * as d3 from 'd3';

function PaperSizeVisualizer({ startWidth, startLength, finishWidth, finishLength }) {
    const svgRef = useRef();
    const [width, setWidth] = useState(window.innerWidth * 0.9);  // 90%   

    useEffect(() => {
        window.addEventListener('resize', () => {
            setWidth(window.innerWidth * 0.9);  //      
        });

        return () => {
            window.removeEventListener('resize', () => setWidth(window.innerWidth * 0.9));
        }
    }, []);

    useEffect(() => {
        if (!startWidth || !startLength || !finishWidth || !finishLength) return;

        const svg = d3.select(svgRef.current);
        svg.selectAll("*").remove();

        const scale = width / startWidth;  //  ,  startWidth  90%  
        const svgHeight = startLength * scale;

        const finishWidthScaled = finishWidth * scale;
        const finishLengthScaled = finishLength * scale;

        const widthRatioNormal = Math.floor(width / finishWidthScaled);
        const lengthRatioNormal = Math.floor(svgHeight / finishLengthScaled);

        const remainingWidth1 = width - (widthRatioNormal * finishWidthScaled);
        const remainingLength1 = svgHeight;

        const additionalPrimaryRatio1 = Math.floor(remainingWidth1 / finishLengthScaled) * Math.floor(remainingLength1 / finishWidthScaled);

        const widthRatioAlternate = Math.floor(width / finishLengthScaled);
        const lengthRatioAlternate = Math.floor(svgHeight / finishWidthScaled);

        const remainingWidth2 = width - (widthRatioAlternate * finishLengthScaled);
        const remainingLength2 = svgHeight;

        const additionalAlternateRatio2 = Math.floor(remainingWidth2 / finishWidthScaled) * Math.floor(remainingLength2 / finishLengthScaled);

        let widthRatio, lengthRatio, chosenWidth, chosenHeight;
        if (widthRatioNormal * lengthRatioNormal + additionalPrimaryRatio1 > widthRatioAlternate * lengthRatioAlternate + additionalAlternateRatio2) {
            widthRatio = widthRatioNormal;
            lengthRatio = lengthRatioNormal;
            chosenWidth = finishWidthScaled;
            chosenHeight = finishLengthScaled;
        } else {
            widthRatio = widthRatioAlternate;
            lengthRatio = lengthRatioAlternate;
            chosenWidth = finishLengthScaled;
            chosenHeight = finishWidthScaled;
        }

        svg.attr("width", width)
            .attr("height", svgHeight)
            .append("rect")
            .attr("x", 0)
            .attr("y", 0)
            .attr("width", width)
            .attr("height", svgHeight)
            .attr("stroke", "black")
            .attr("fill", "lightgrey");

        for (let i = 0; i < widthRatio; i++) {
            for (let j = 0; j < lengthRatio; j++) {
                svg.append("rect")
                    .attr("x", i * chosenWidth)
                    .attr("y", j * chosenHeight)
                    .attr("width", chosenWidth)
                    .attr("height", chosenHeight)
                    .attr("fill", "yellow")
                    .attr("stroke", "black");
            }
        }

        //     
        if (chosenWidth === finishWidthScaled && chosenHeight === finishLengthScaled) {
            const remainingWidth = width - (widthRatio * finishWidthScaled);
            const remainingHeight = svgHeight;

            const additionalWidthRatio = Math.floor(remainingWidth / finishLengthScaled);
            const additionalLengthRatio = Math.floor(remainingHeight / finishWidthScaled);

            for (let i = 0; i < additionalWidthRatio; i++) {
                for (let j = 0; j < additionalLengthRatio; j++) {
                    svg.append("rect")
                        .attr("x", widthRatio * finishWidthScaled + i * finishLengthScaled)
                        .attr("y", j * finishWidthScaled)
                        .attr("width", finishLengthScaled)
                        .attr("height", finishWidthScaled)
                        .attr("fill", "yellow")
                        .attr("stroke", "black");
                }
            }
        } else {
            const remainingWidth = width - (widthRatio * finishLengthScaled);
            const remainingHeight = svgHeight;

            const additionalWidthRatio = Math.floor(remainingWidth / finishWidthScaled);
            const additionalLengthRatio = Math.floor(remainingHeight / finishLengthScaled);

            for (let i = 0; i < additionalWidthRatio; i++) {
                for (let j = 0; j < additionalLengthRatio; j++) {
                    svg.append("rect")
                        .attr("x", widthRatio * finishLengthScaled + i * finishWidthScaled)
                        .attr("y", j * finishLengthScaled)
                        .attr("width", finishWidthScaled)
                        .attr("height", finishLengthScaled)
                        .attr("fill", "yellow")
                        .attr("stroke", "black");
                }
            }
        }
    }, [startWidth, startLength, finishWidth, finishLength, width]);

    return <svg ref={svgRef}></svg>;
}

export default PaperSizeVisualizer;
