import React, { useEffect, forwardRef } from 'react';
import PropTypes from 'prop-types';
import { IMaskInput } from 'react-imask';
import { NumericFormat } from 'react-number-format';
import { FormControl, InputLabel, Select, MenuItem, Box, Input, Stack } from '@mui/material';
import PaperSizeVisualizer from './PaperSizeVisualizer';

const TextMaskCustom = forwardRef(function TextMaskCustom(props, ref) {
    const { onChange, ...other } = props;
    return (
        <IMaskInput
            {...other}
            mask="(#00) 000-0000"
            definitions={{
                '.': /[1-9]/,
            }}
            inputRef={ref}
            onAccept={(value) => onChange({ target: { name: props.name, value } })}
            overwrite
            inputMode="tel"
        />
    );
});

TextMaskCustom.propTypes = {
    name: PropTypes.string.isRequired,
    onChange: PropTypes.func.isRequired,
};

const NumericFormatCustom = forwardRef(function NumericFormatCustom(props, ref) {
    const { onChange, ...other } = props;
    return (
        <NumericFormat
            {...other}
            getInputRef={ref}
            onValueChange={(values) => {
                onChange({
                    target: {
                        name: props.name,
                        value: values.value,
                    },
                });
            }}
            thousandSeparator
            decimalSeparator="."
            valueIsNumericString
            inputMode="decimal"


        />
    );
});

NumericFormatCustom.propTypes = {
    name: PropTypes.string.isRequired,
    onChange: PropTypes.func.isRequired,
};

const AppContent = ({
                        selectedPaper, setSelectedPaper, paperTypes, selectedType, setSelectedType, selectedWeight, setSelectedWeight,
                        startWidth, setStartWidth, startLength, setStartLength, finishWidth, setFinishWidth, finishLength, setFinishLength,
                        requiredSheets, setRequiredSheets, calculateResult, resultInches
                    }) => {

    useEffect(() => {
        calculateResult();
    }, [selectedPaper, selectedType, selectedWeight, startWidth, startLength, finishWidth, finishLength, requiredSheets]);

    const handleChange = (event) => {
        const { name, value } = event.target;
        switch(name) {
            case 'startWidth':
                setStartWidth(value);
                break;
            case 'startLength':
                setStartLength(value);
                break;
            case 'finishWidth':
                setFinishWidth(value);
                break;
            case 'finishLength':
                setFinishLength(value);
                break;
            case 'requiredSheets':
                setRequiredSheets(value);
                break;
            default:
                break;
        }
    };

    return (
        <div className="App">
            <header className="App-header">
                <div className='menu'>
                    <div className='menu2'>
                        <Box display="flex" justifyContent="space-between" marginTop={2}>
                            <div className='menu3'>
                                <FormControl fullWidth margin="normal">
                                    <InputLabel id="paper-select-label">Name</InputLabel>
                                    <Select
                                        labelId="paper-select-label"
                                        value={selectedPaper}
                                        onChange={e => setSelectedPaper(e.target.value)}
                                        label="Name"
                                    >
                                        {paperTypes.map((paper) => (
                                            <MenuItem key={paper.name} value={paper.name}>{paper.name}</MenuItem>
                                        ))}
                                    </Select>
                                </FormControl>
                            </div>

                            <div className='menu3'>
                                <FormControl fullWidth margin="normal">
                                    <InputLabel id="type-select-label">Type</InputLabel>
                                    <Select
                                        labelId="type-select-label"
                                        value={selectedType}
                                        onChange={e => setSelectedType(e.target.value)}
                                        disabled={!selectedPaper}
                                        label="Type"
                                    >
                                        {selectedPaper && paperTypes.find(paper => paper.name === selectedPaper)?.details.map(detail => (
                                            <MenuItem key={detail.type} value={detail.type}>{detail.type}</MenuItem>
                                        ))}
                                    </Select>
                                </FormControl>
                            </div>

                            <div className='menu3'>
                                <FormControl fullWidth margin="normal">
                                    <InputLabel id="weight-select-label">Weight</InputLabel>
                                    <Select
                                        labelId="weight-select-label"
                                        value={selectedWeight}
                                        onChange={e => setSelectedWeight(e.target.value)}
                                        disabled={!selectedType}
                                        label="Weight"
                                    >
                                        {selectedType && paperTypes.find(paper => paper.name === selectedPaper)?.details.find(detail => detail.type === selectedType)?.weights.map(weight => (
                                            <MenuItem key={weight.value} value={weight.value}>{weight.value}</MenuItem>
                                        ))}
                                    </Select>
                                </FormControl>
                            </div>
                        </Box>
                    </div>

                    {/* Dimensions input */}
                    <div className='menu2'>
                        <Box display="flex" justifyContent="space-between" marginTop={2}>
                            <Stack direction="row" spacing={2}>
                                <FormControl variant="standard">
                                    <InputLabel htmlFor="formatted-start-length-input">Start Width (in.)</InputLabel>
                                    <Input
                                        value={startLength}
                                        onChange={handleChange}
                                        name="startLength"
                                        id="formatted-start-length-input"
                                        inputComponent={NumericFormatCustom}
                                        inputProps={{ inputMode: 'decimal' }}

                                    />
                                </FormControl>

                                <FormControl variant="standard">
                                    <InputLabel htmlFor="formatted-start-width-input">Start Length (in.)</InputLabel>
                                    <Input
                                        value={startWidth}
                                        onChange={handleChange}
                                        name="startWidth"
                                        id="formatted-start-width-input"
                                        inputComponent={NumericFormatCustom}
                                        inputProps={{ inputMode: 'decimal' }}
                                    />
                                </FormControl>

                            </Stack>
                        </Box>
                    </div>

                    <div className='menu2'>
                        <Box display="flex" justifyContent="space-between" marginTop={2}>
                            <Stack direction="row" spacing={2}>
                                <FormControl variant="standard">
                                    <InputLabel htmlFor="formatted-finish-width-input">Finish Width (in.)</InputLabel>
                                    <Input
                                        value={finishWidth}
                                        onChange={handleChange}
                                        name="finishWidth"
                                        id="formatted-finish-width-input"
                                        inputComponent={NumericFormatCustom}
                                        inputProps={{ inputMode: 'decimal' }}
                                    />
                                </FormControl>
                                <FormControl variant="standard">
                                    <InputLabel htmlFor="formatted-finish-length-input">Finish Length (in.)</InputLabel>
                                    <Input
                                        value={finishLength}
                                        onChange={handleChange}
                                        name="finishLength"
                                        id="formatted-finish-length-input"
                                        inputComponent={NumericFormatCustom}
                                        inputProps={{ inputMode: 'decimal' }}
                                    />
                                </FormControl>
                            </Stack>
                        </Box>
                    </div>

                    <PaperSizeVisualizer
                        startWidth={startWidth}
                        startLength={startLength}
                        finishWidth={finishWidth}
                        finishLength={finishLength}
                    />

                    <FormControl variant="standard">
                        <InputLabel htmlFor="formatted-finish-length-input">Required number of sheets</InputLabel>
                        <Input
                            fullWidth
                            label="Required number of sheets"
                            value={requiredSheets}
                            onChange={handleChange}
                            name="requiredSheets"
                            id="formatted-required-sheets-input"
                            inputComponent={NumericFormatCustom}
                            inputProps={{ inputMode: 'decimal' }}
                            margin="normal"
                        />
                    </FormControl>


                    <div className='resul'>
                        <div>{resultInches}</div>
                    </div>
                </div>
            </header>
        </div>
    );
};

export default AppContent;
