import React, { useState } from 'react';
import AppContent from "./AppContent";
import './App.css';
import paperTypes from "./constants/paperTypes";

function App() {
  const [selectedPaper, setSelectedPaper] = useState('');
  const [selectedType, setSelectedType] = useState('');
  const [selectedWeight, setSelectedWeight] = useState('');
  const [requiredSheets, setRequiredSheets] = useState('');
  const [startWidth, setStartWidth] = useState('');
  const [finishWidth, setFinishWidth] = useState('');
  const [startLength, setStartLength] = useState('');
  const [finishLength, setFinishLength] = useState('');
  const [resultInches, setResultInches] = useState('');

  const calculateResult = () => {
    const selectedPaperDetails = paperTypes.find(paper => paper.name === selectedPaper)?.details;
    const selectedTypeDetail = selectedPaperDetails?.find(detail => detail.type === selectedType);
    const weightDetail = selectedTypeDetail?.weights.find(weight => weight.value === parseInt(selectedWeight));

    if (!weightDetail || !requiredSheets || requiredSheets <= 0) {
      setResultInches("Please ensure all selections are made and required sheets are entered correctly.");
      return;
    }

    if (startWidth && finishWidth && startLength && finishLength) {
      const widthRatio = Math.floor(Number(startWidth) / Number(finishWidth));
      const lengthRatio = Math.floor(Number(startLength) / Number(finishLength));
      const primaryRatio = widthRatio * lengthRatio;

      const remainingWidth1 = Number(startWidth) - (widthRatio * Number(finishWidth));
      const remainingLength1 = Number(startLength);

      const additionalPrimaryRatio1 = Math.floor(remainingWidth1 / Number(finishLength)) * Math.floor(remainingLength1 / Number(finishWidth));

      const alternateWidthRatio = Math.floor(Number(startWidth) / Number(finishLength));
      const alternateLengthRatio = Math.floor(Number(startLength) / Number(finishWidth));
      const alternateRatio = alternateWidthRatio * alternateLengthRatio;

      const remainingWidth2 = Number(startWidth) - (alternateWidthRatio * Number(finishLength));
      const remainingLength2 = Number(startLength);

      const additionalAlternateRatio2 = Math.floor(remainingWidth2 / Number(finishWidth)) * Math.floor(remainingLength2 / Number(finishLength));

      const bestRatio = Math.max(primaryRatio + additionalPrimaryRatio1, alternateRatio + additionalAlternateRatio2);

      if (bestRatio === 0) {
        alert("Dimensions result in zero output. Please check your dimensions.");
        return;
      }

      const totalSheetsNeeded = Number(requiredSheets) / bestRatio;
      const pricePerUnit = weightDetail.pricePerUnit / 100;
      const total = totalSheetsNeeded * pricePerUnit;
      const totalCost = total * 25.4;

      setResultInches(`Total inches: ${total.toFixed(3)}, Total mm: ${totalCost.toFixed(2)}, Number of elements: ${bestRatio}`);
    } else {
      setResultInches("Please ensure all dimensions are entered correctly.");
    }
  };

  return (
      <div className="App">
        <header className="App-header">
          <AppContent
              selectedPaper={selectedPaper} setSelectedPaper={setSelectedPaper} paperTypes={paperTypes}
              selectedType={selectedType} setSelectedType={setSelectedType} selectedWeight={selectedWeight}
              setSelectedWeight={setSelectedWeight} startWidth={startWidth} setStartWidth={setStartWidth}
              startLength={startLength} setStartLength={setStartLength} finishWidth={finishWidth}
              setFinishWidth={setFinishWidth} finishLength={finishLength} setFinishLength={setFinishLength}
              requiredSheets={requiredSheets} setRequiredSheets={setRequiredSheets} calculateResult={calculateResult}
              resultInches={resultInches}
          />
        </header>
      </div>
  );
}

export default App;
